<template>
  <div class="center mt-5">
    <div class=" p-5 rounded-3xl bg-gray-200 w-32">
      <span
        >Total: <b class=" text-3xl">{{ dataListLocal.length }}</b></span
      >
    </div>

    <vs-table striped class=" mt-5">
      <template #header>
        <vs-input v-model="search" border placeholder="Buscar" />
      </template>

      <template #thead>
        <vs-tr>
          <vs-th v-for="(item, i) in columns" :key="i">
            {{ item.text }}
          </vs-th>
        </vs-tr>
      </template>

      <template #tbody>
        <vs-tr
          :key="i"
          v-for="(tr, i) in $vs.getSearch(dataListLocal, search)"
          :data="tr"
        >
          <vs-td
            @click="$emit('edit', tr)"
            :edit="i2 == 0"
            v-for="(item, i2) in columns"
            :key="i2"
          >
            {{ tr[item.name] }}
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No exiten datos 
      </template>
    </vs-table>
  </div>
</template>
<script>
export default {
  props: {
    dataList: {
      type: Array,
    },
  },

  data: () => ({
    columns: [
      {
        name: "username",
        text: "Usuario",
      },

      {
        name: "email",
        text: "Email",
      },

      {
        name: "group",
        text: "Grupo",
      },
    ],
    search: "",
    dataListLocal: [],
  }),

  watch: {
    dataList(newValue) {
      this.dataListLocal = newValue;
    },
  },
};
</script>
