<template>
  <vs-dialog blur prevent-close not-center v-model="dialogLocal">
    <template #header>
      <h4><b>Usuario</b></h4>
    </template>

    <form class="w-full flex flex-col">
      <div>
        <span class="font-medium text-xl text-green-400">
          Datos de Perfil
        </span>

        <div class="mt-5">
          <label>Seleccione el grupo:</label>
          <v-select
            label="name"
            index="id"
            v-model="group"
            :options="get_group"
          ></v-select>
        </div>

        <div class="mt-5 ">
          <label>Ingrese su correo electr贸nico:</label>
          <input
            v-model="email"
            placeholder="Correo Electr贸nico"
            type="email"
            class="p-2 px-4 border-gray-200 border-2 rounded-lg block w-full focus:outline-none"
            @keypress.enter="save"
          />
        </div>
      </div>

      <div class=" mt-5">
        <span class="font-medium text-xl text-green-400">
          Datos de usuario
        </span>

        <div class="mt-5">
          <label class=" text-sm text-gray-500">Usuario</label>
          <vs-input
            :readonly="id"
            v-model.trim="username"
            autofocus
            id="txtusername"
          >
            <template #icon>
              <i class="fas fa-user text-green-400"></i>
            </template>
          </vs-input>
        </div>

        <div v-if="!id" class=" mt-5">
          <label class=" text-sm text-gray-500">Contrase帽a</label>
          <vs-input
            type="password"
            v-model.trim="password"
            @keypress.enter="save"
          >
            <template #icon>
              <i class="fas fa-key text-green-400"></i>
            </template>
          </vs-input>
        </div>
      </div>
    </form>

    <p class=" text-xs text-blue-400 my-5">
      Podr谩 iniciar sesi贸n con su correo o su usuario.
    </p>

    <template #footer>
      <div class="flex justify-between focus:outline-none">
        <div>
          <vs-button
            v-if="id"
            transparent
            class="focus:outline-none"
            color="danger"
            @click="dialogConfirm = true"
          >
            Eliminar
          </vs-button>
        </div>

        <div class="flex">
          <vs-button class="focus:outline-none" @click="save">
            Guardar
          </vs-button>
          <vs-button dark transparent @click="exit" class="focus:outline-none">
            Cancelar
          </vs-button>
        </div>
      </div>
    </template>

    <vs-dialog
      width="550px"
      not-center
      blur
      v-model="dialogConfirm"
      class=" z-50"
    >
      <template #header>
        <h4 class="not-margin">Eliminar <b>Usuario</b></h4>
      </template>

      <div class="con-content">
        <p>
          Esta seguro que desea eliminar el usuario? <br />
          <b>Esta acci贸n no se puede deshacer</b>.
        </p>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button @click="remove" transparent>
              Si
            </vs-button>
            <vs-button @click="dialogConfirm = false" dark transparent>
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>
  </vs-dialog>
</template>

<script>
export default {
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },

    userData: {
      type: Object,
      default: () => {
        return null;
      },
    },
  },

  data() {
    return {
      dialogLocal: false,
      dialogConfirm: false,
      group: "",
      username: "",
      password: "",
      email: "",
      id: null,
    };
  },

  computed: {
    get_group() {
      return this.$store.getters["user/get_group"];
    },
  },

  watch: {
    dialog(newValue) {
      this.dialogLocal = newValue;
    },

    dialogLocal(newValue) {
      if (!newValue) {
        this.$emit("changedialog", newValue);
      }
    },

    userData(newValue) {
      if (newValue) {
        this.group = this.get_group.find(
          (item) => item.id == newValue.group_id
        );

        this.username = newValue.username;
        this.password = newValue.password;
        this.email = newValue.email;
        this.id = newValue.id;
      }
    },
  },

  methods: {
    clearData() {
      this.group = "";
      this.username = "";
      this.password = "";
      this.email = "";
      this.id = null;
    },

    async remove() {
      const loading = this.$vs.loading();
      this.dialogConfirm = false;
      let data = [];
      try {
        await this.$store
          .dispatch("user/delete_user", { user: this.id })
          .then((res) => (data = res));

        if (!data) {
          this.$vs.notification({
            title: "Usuario",
            text: "No se pudo eliminar, intentelo denuevo! ",
            color: "warning",
          });
          return;
        }

        this.$vs.notification({
          title: "Usuario",
          text: "Usuario eliminado correctamente! ",
          color: "success",
        });

        this.exit();
      } catch (error) {
        this.$vs.notification({
          title: "Usuario",
          text: "No se pudo eliminar, debido a un error en el servidor! ",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },

    validar() {
      try {
        if (
          this.group.length == 0 ||
          this.email.length == 0 ||
          this.username.length == 0 ||
          (!this.id && this.password.length == 0)
        ) {
          this.$vs.notification({
            title: "Usuario",
            text: "Por favor, verifique los campos! ",
            color: "warning",
          });
          return false;
        }

        return true;
      } catch (error) {
        this.$vs.notification({
          title: "Usuario",
          text: "Por favor, verifique los campos! ",
          color: "danger",
        });
        return false;
      }
    },

    exit() {
      this.$emit("closedialog");
      this.dialogLocal = false;
      this.clearData();
    },

    async save() {
      const loading = this.$vs.loading();
      try {
        if (!this.validar()) return;

        let result = [];
        let obj = {
          email: this.email,
          username: this.username,
          password: this.password,
          group: this.group.id,
        };

        if (!this.id) {
          await this.$store.dispatch("user/add_user", obj).then((res) => {
            result = res;
          });
        } else {
          delete obj.email;
          delete obj.username;
          delete obj.password;
          obj.user = this.id;

          await this.$store.dispatch("user/add_user_group", obj).then((res) => {
            result = res;
          });

          await this.$store.dispatch("user/set_user");
        }

        if (!result) {
          this.$vs.notification({
            title: "Usuario",
            text: "No se pudo realizar la acci贸n, vuelve a intentarlo! ",
            color: "danger",
          });
          return;
        }

        this.$vs.notification({
          title: "Usuario",
          text: "Acci贸n completada correctamente! ",
          color: "success",
        });
        this.exit();
      } catch (error) {
        console.error(error);
      } finally {
        loading.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
