<template>
  <div>
    <div class=" flex justify-between">
      <vs-button class=" focus:outline-none" @click="add()">
        Agregar Nuevo
      </vs-button>
      <h1 class=" text-3xl text-gray-400 font-medium">Usuarios</h1>
    </div>

    <list :dataList="get_user" @edit="edit" />
    <add
      :dialog="dialog"
      @changedialog="dialog = false"
      @closedialog="closeDialog()"
      :userData="userData"
    />
  </div>
</template>

<script>
import Add from "./components/Add.vue";
import List from "./components/List.vue";
export default {
  components: { List, Add },

  data() {
    return {
      dialog: false,
      userData: null,
    };
  },

  computed: {
    get_user() {
      return this.$store.getters["user/get_user"];
    },

    get_group() {
      return this.$store.getters["user/get_group"];
    },
  },

  methods: {
    add() {
      this.userData = null;
      this.dialog = true;
    },

    edit(e) {
      this.userData = e;
      if (this.userData) {
        this.dialog = true;
      }
    },

    closeDialog() {
      this.dialog = false;
      this.userData = null;
    },
  },

  mounted() {
    this.$store.dispatch("user/set_user");
    this.$store.dispatch("user/set_group");
    this.dialog = false;
  },
};
</script>

<style lang="scss" scoped></style>
